.admin_right{
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.admin_right_form{
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.admin_right h2{
  text-transform: capitalize;
  font-weight: 600;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }

  th, td {
    text-align: left;
    padding: 0.6rem;
  }

  th {
    background-color: #f2f2f2;
    text-align: center;
  }

td{
  border-bottom: 1px solid #c1c1c1;
}

  .name{
    font-weight: 600;
    text-transform: capitalize;
  }

  .edit, .remove {
    text-align: center;
  }

  .edit button{
    padding: 0.5rem 1rem;
    border-radius: 9px;
    border: none;
    text-transform: capitalize;
    font-weight: 500;
    color: black;
    background-color: #ffbe00;
    cursor: pointer;
  }

.edit button:hover{
  box-shadow: #ffd351 0px 5px 15px;
}

.remove button{
    border: none;
    background-color: transparent;
    font-size: 1.7rem;
    color: crimson;
    cursor: pointer;
}
