.admin_page{
    width: 100%;
    display: flex;
    padding: 2rem 3rem;
}

.admin_menu{
    width: 25%;
}

.admin_right{
    width: 100%;
    padding: 1rem 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.admin_right h1{
    font-size: 1.8rem;
    font-weight: 600;
}

@media (max-width: 640px) {
    .admin_page{
        width: 100%;
        padding: 2rem 1rem;
        display: inline-block;
    }

    .admin_menu{
        width: 100%;
    }

    .admin_right{
        width: 100%;
        padding: 1rem 0;
        justify-content: center;
    }

    .admin_right h1{
        font-size: 1rem;
        font-weight: 600;
    }
}