form{
    display: flex;
    gap: 1rem;
    align-items: center;
}

.form_box input{
    min-width: 20rem;
    padding: 0.7rem;
    font-weight: 500;
    font-size: 0.9rem;
}

.form-btn{
    background-color: #ffbe00;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 9px;
    font-weight: 500;
    font-size: 1rem;
}

@media (max-width: 640px) {
    .form_box input{
        min-width: 100%;
        padding: 0.7rem;
        font-weight: 500;
        font-size: 0.9rem;
    }
}