
.admin_page_left{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem;
}

.admin_page_left h1{
    font-size: 1.5rem;
    text-align: center;
}

.admin_page_left_btn{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.btns{
    width: 100%;
    color: black;
    font-size: 1rem;
    font-weight: 500;
    padding: 1rem 0;
    background-color: #ffbe00;
    border: none;
    border-radius: 9px;
    cursor: pointer;
}